import style from "./header.module.css"
import { Anchor } from 'antd'

const { Link } = Anchor

const Burger = (props) => {
    
    return(
        <div className={style.burger_menu} onClick={props.closeBurger}>
        <Anchor>
            <Link className={style.nav_item} href='/maunisse-test/favorites' title={<a>ИЗБРАННОЕ</a>} />
            <Link className={style.nav_item} href='/maunisse-test#o_nas_jakor' title={<a>О НАС</a>} />
            <Link className={style.nav_item} href='/maunisse-test#product_jakor' title={<a>КАТАЛОГ</a>} />
            <Link className={style.nav_item} href='/maunisse-test#footer' title={<a>КОНТАКТЫ</a>} />
        </Anchor>
        </div>
    )
}
export default Burger;