// import { Link } from 'react-router-dom'
import style from './header.module.css'
import { Anchor } from 'antd'

const { Link } = Anchor

const Header = (props) => {
    return (
        <header>
            <Anchor>
            <div className={style.header_section}>
            
                {/*так обозначается ссылка на главную страницу*/}
                
                <Link href='/maunisse-test' title={<img src={process.env.PUBLIC_URL + '/img/header/logo.png'} alt = "" />}></Link>
                
                    <div className={style.burger}>
                        <div className={style.burger_height} onClick={props.openBurger}><div className={style.menu}></div></div>
                        <div className={style.put_cart} onClick={props.openCart}><img src={process.env.PUBLIC_URL +"/img/header/put_cart.png"}/><span className={style.count_cart_item}>{props.cartItems.length}</span></div>
                    </div>

                <nav>

                    {/*копия и замена тегу <a> и в App.css мы так же задаем a{text-decoration: none}*/}
                    <Link className={style.nav_item} href='/maunisse-test/favorites' title={<a>ИЗБРАННОЕ</a>} />
                    <Link className={style.nav_item} href='/maunisse-test#o_nas_jakor' title={<a>О НАС</a>} />
                    <Link className={style.nav_item} href='/maunisse-test#product_jakor' title={<a>КАТАЛОГ</a>} />
                    <Link className={style.nav_item} href='/maunisse-test#footer' title={<a>КОНТАКТЫ</a>} />
                    <span className={style.nav_item} onClick={props.openCart}><a>КОРЗИНА</a><span className={style.count_cart_item}>{props.cartItems.length}</span></span>
                    
                </nav>
                
            </div>
            </Anchor>
        </header>
    )
}

export default Header