import React, { useState } from "react"
import style from './card.module.css'
import { AppContext } from "../../../App";

const Card = (props) => {

const context=React.useContext(AppContext)

//ранее мы заменили этот стейт контекстом в App
 // const [added, setAdded] = useState(props.isAdded);
  // const [favorite, setFavorite] = useState(props.isFavorite)

  const onClickPlus = () => {
    let id = props.id
    let title = props.title
    let description = props.description
    let price = props.price
    let img = props.img
    props.onPlus({ id, title, description, price, img })
    // ! - задает отрицание. Отправит значение false по умолчанию
    // убираем статус по умолчанию так как добавили проверку статуса кнопки по нажатию через контекст
    //setAdded(!added)
  }
  const onClickFavorite = () => {
    let id = props.id
    let title = props.title
    let description = props.description
    let price = props.price
    let img = props.img

    props.onFavorite({ id, title, description, price, img });

    //setFavorite(!favorite);
  }
  return (
    <div className={style.product_item}>
      {
        props.isLoading ?
          <div className={style.contentLoader}>
            {/* <ContentLoader
              speed={2}
              width={250}
              height={250}
              viewBox="0 0 250 250"
              backgroundColor="#ffffff"
              foregroundColor="#ffffff"
              {...props}
            >
            </ContentLoader> */}
            </div> :
          <>
            {
              // favorite === true ?
              //   <button className={style.favorite_btn_added} onClick={onClickFavorite}>Убрать из избранных</button> : <button className={style.favorite_btn} onClick={onClickFavorite} >Добавить в избранное</button>
                context.itemFavorited(props.id) === true ?
                <button className={style.favorite_btn_added} onClick={onClickFavorite}>Убрать из избранных</button> : <button className={style.favorite_btn} onClick={onClickFavorite} >Добавить в избранное</button>
            }
            <img className={style.product_img} src={process.env.PUBLIC_URL + props.img} alt={props.title} />
            <div className={style.block_with_text_for_product}>
              <p className={style.product_title}>{props.title}</p>
              <p>{props.description}</p>
              <div className={style.product_price}>
                <span>{props.price} руб</span>
                {/* заменяли прошлый стейт на функцию проверки состояния кнопки при нажатии через контекст
                <button className={added ? style.check_btn : style.plus_btn} onClick={onClickPlus}>
                  <img src={added ? '/img/katalog/check.png' : '/img/katalog/icons8-plus-24.png'} alt="" />*/}
                <button className={context.itemAdded(props.id) ? style.check_btn : style.plus_btn} onClick={onClickPlus}>
                  <img src={context.itemAdded(props.id) ? process.env.PUBLIC_URL + '/img/katalog/check.png' : process.env.PUBLIC_URL + '/img/katalog/icons8-plus-24.png'} alt="" />
                </button>
              </div>
            </div>
          </>
      }
    </div>

  )

}
export default Card