import React, { useState } from "react"
import style from './favoritesCard.module.css'

//вместо (props) могли методом деструктуризации добавить все let  - так - (props, {id} и т.д.) и тогда прописывать например каждый раз let id = props.id было бы не нужно
const FavoritesCard = (props) => {
  const [added, setAdded] = useState(false);
  //const [favorite, setFavorite] = useState(true)

  const onClickPlus = () => {
    let id = props.id
    let title = props.title
    let description = props.description
    let price = props.price
    let img = props.img
    props.onPlus({id,title, description, price, img})

    setAdded(!added)
}
const onClickFavorite = () => {

  props.onFavorite(props.id);

  //setFavorite(!favorite);
}
  return (
    <div className={style.product_item}>
      {/* {
      favorite === true ? 
      <button className={style.favorite_btn_added} onClick={props.onClickFavorite}>Товар в избранном</button> : <button className={style.favorite_btn} onClick={onClickFavorite} >Добавить в избранное</button>
    } */}

<button className={style.favorite_btn_added} onClick={onClickFavorite}>Удалить из избранного</button>

      <img className={style.product_img} src={process.env.PUBLIC_URL + props.img} alt={props.title} />
      <div className={style.block_with_text_for_product}>
        <p className={style.product_title}>{props.title}</p>
        <p>{props.description}</p>
        <div className={style.product_price}>
          <span>{props.price} руб</span>
          <button className={added ? style.check_btn : style.plus_btn} onClick={onClickPlus}>
          <img src = {added ? process.env.PUBLIC_URL +'/img/katalog/check.png' : process.env.PUBLIC_URL +'/img/katalog/icons8-plus-24.png'} alt = "" />
          </button>
        </div>
      </div>
    </div>
  )
}

export default FavoritesCard