import axios from 'axios';
import React from "react"
import Card from "./card/Card"
import style from './products.module.css'



const Products = (props) => {

  //пример без асинхрона

  // const onAddToCart = (objCart) => {
  //   //обернем в трайкеш - обработчик ошибок который нам подскажет если будут ошибки
  //   try {
  //     //если товар уже такой есть то при повторном клике на галку он удалится (типа удаляем из добавленных). Однако если перезагрузить страницу то возникает проблема! - во-первых галка сбрасывается и снова появляется +, а при нажатии на него повтор не удаляется
  //     if (props.cartItems.find((item) => Number(item.id) === Number(objCart.id))) {
  // //это отвечает ща удаление с бекенда
  //       axios.delete(`http://localhost:3000/cart/${objCart.id}`)
  // // а это - с фронтенда
  //       props.setCartItems(prev => prev.filter(item => Number(item.id) !== Number(objCart.id)))
  //     } else {
  //       //...props.setCartItems = верни все что находится в массиве на данный момент и добавь туда еще один объект objCart
  //       //obj - и добавь туда новый объект
  //       axios.post('http://localhost:3000/cart', objCart)
  //       props.setCartItems([...props.cartItems, objCart]);
  //     }

  //   } catch {
  //     alert('Не удалось добавить товар в корзину')
  //   }

  // }

  //пример с асинхроном (прогружаем принудительно данные с сервера в корзину и пока не прогрузятся не откроются) делали для того чтобы убрать ошибки некорректного удаления с дурацкого мокапи который сам обновляет id-ы

  // const onAddToFavorite = async (objFavorite) => {
  //   try {

  //     if (props.favoritesItems.find(obj => Number(obj.id) === Number(objFavorite.id))) {
  //       axios.delete(`https://634d28fdf5d2cc648e9e1f2c.mockapi.io/favorites/${objFavorite.id}`)
  //     } else {
  // //забираются только нужные данные из ссылки (методом деструктуризации)
  // //await - дождись пока выполнится запрос
  //       const { data } = await axios.post('https://634d28fdf5d2cc648e9e1f2c.mockapi.io/favorites', objFavorite)
  //       props.setFavoritesItems([...props.favoritesItems, data])
  //     }
  //   } catch {
  //     alert("Не удалось добавить товар в избранное")
  //   }
  // }

  // вынесли наши условия в отдельные функции

  const onAddToCart = async (objCart) => {
    try {

      const findCartItem = props.cartItems.find(item => item.id === objCart.id)

      if (findCartItem) {
        axios.delete(`https://my-json-server.typicode.com/maunisse/json-server/cart/${findCartItem.id}`)
        props.setCartItems(prev => prev.filter(item => (item.id) !== (findCartItem.id)))
      } else {
        const { data } = await axios.post('https://my-json-server.typicode.com/maunisse/json-server/cart', objCart)
        props.setCartItems([...props.cartItems, data]);
        //могли написать и так
        // const res=await axios.post('http://localhost:3000/cart', objCart)
        // props.setCartItems([...props.cartItems, res.data]);
      }
    } catch {
      alert('Не удалось добавить товар в корзину')
    }
  }

  const onAddToFavorite = async (objFavorite) => {
    try {
      const findFavoriteItem = props.favoritesItems.find(obj => obj.id === objFavorite.id)

      if (findFavoriteItem) {
        axios.delete(`https://my-json-server.typicode.com/maunisse/json-server/favorites/${findFavoriteItem.id}`)
        props.setFavoritesItems(prev => prev.filter(obj => (obj.id) !== (objFavorite.id)))
      } else {
        const { data } = await axios.post('https://my-json-server.typicode.com/maunisse/json-server/favorites', objFavorite)
        props.setFavoritesItems([...props.favoritesItems, data])
      }
    } catch {
      alert("Не удалось добавить товар в избранное")
    }
  }

  const onSearchInput = (inputValue) => {
    //target - ловит по триггеру - введенное данное value в поле input
    props.setSearch(inputValue.target.value)
  }

//для прелоада
const renderCard=()=>{
/*перед тем как ты (props.items) промапишься (вызовешь карточки товара), надо отфильтроваться:
            все что придет в атрибут item
            из поиска по title
            toLowerCase() -  как будто всё написано маленькими буквами
            includes - строгий поиск посимвольно
            props.search.toLowerCase() - все вводимое будет автоматом переводиться в мелкие буквы
            
            */
const filterItems = props.items.filter((item) => item.title.toLowerCase().includes(props.search.toLowerCase()))

  //верни либо пустой пассив либо карточки
return (props.loading ? [...Array(6)] : filterItems).map((obj,index) => {
              return (
                <Card
                  // создавали переменную myId для обхода глюка в макапи! и прописывали функции с ней
                  //id={obj.id}
                  //где map везде ставим key просто чтобы он был, так как react требует его наличие
                  //key={obj.id}

                  //добавили индекс чтобы вытащить отдельно key специально для устранения ошибки браузера!!
                  key={index}
                  //передали все свойства
                  {...obj}

                  isLoading={props.loading}
                  //some вернет тру или фолс. Проверка состояния кнопки "+\галка"
                  //убрали потому что использовали контекст
                 // isAdded={props.cartItems.some((objIsAdded)=>objIsAdded.id===obj.id)}
                 // isFavorite={props.favoritesItems.some((obIsFavorite)=>obIsFavorite.id===obj.id)}

                  onFavorite={
                    (favoritesObj) => {
                      onAddToFavorite(favoritesObj)
                    }
                  }

                  //в атрибут cartObj будет заноситься по клику данные карточки и заноситься методом onAddToCart в корзину
                  onPlus={
                    (cartObj) => {
                      onAddToCart(cartObj)
                    }
                  }
                />
              )
            })

}

  return (
    <section>
      <div className={style.product_fon}>
        <div className='block_for_jakor' id="product_jakor"></div>
        <h2>БУКЕТИКИ</h2>

        <div className={style.search}>
          {/*"Все букеты" будут заменяться на Поиск по запросу: введенные данные. Через тернарную запись
          props.search ? - если тут у нас есть какие-то данные, то выведи
          `Поиск по запросу: `+props.search
          иначе выведи 'Все букеты'
          */}
          <h4>{props.search ? `Поиск по запросу: ` + props.search : 'Все букеты'}</h4>
          <div className={style.search_block}>
            <img src={process.env.PUBLIC_URL +"/img/katalog/search.png"} alt="search" />
            {/*onChange = при внесении изменений*/}
            <input onChange={onSearchInput} placeholder="Поиск по товарам" />
          </div>
        </div>

        <br /><br />

        <div className={style.products}>
          {
            renderCard()
          }

        </div>
        <br />
      </div>
    </section>
  )
}

export default Products