import axios from "axios";
import React from "react";
import style from "./favorites.module.css"
import FavoritesCard from "./favoritesCard/FavoritesCard";
import { AppContext } from "../../App";

const Favorites = (props) => {

    //передаем контекст
    const context =React.useContext(AppContext)

    //теперь cartItem и тп передаем не через пропсы а через контекст
    const onAddToCart = (objCart) => {
        // axios.post('http://localhost:3001/cart', objCart)
        axios.post('https://my-json-server.typicode.com/maunisse/json-server/cart', objCart)
        context.setCartItems([...context.cartItems, objCart]);
    }
    const onRemoveFavorites = (id) => {
        // axios.delete(`http://localhost:3001/favorites/${id}`)
        axios.delete(`https://my-json-server.typicode.com/maunisse/json-server/favorites/${id}`)
        context.setFavoritesItems((prev) => prev.filter(item => Number(item.id) !== Number(id)))
    }

    return (
        <section>

            <div className={style.product_fon}>

                    <h4>Избранные товары</h4>

                <br /><br />

                <div className={style.products}>
                    {
                        context.favoritesItems.map(obj => {
                            return (
                                <FavoritesCard
                                    id={obj.id}
                                    key={obj.id}
                                    title={obj.title}
                                    description={obj.description}
                                    price={obj.price}
                                    img={obj.img}

                                    onFavorite={
                                        (id) => {
                                            onRemoveFavorites(id)
                                        }
                                    }

                                    onPlus={
                                        (cartObj) => {
                                            onAddToCart(cartObj)
                                        }
                                    }
                                />
                            )
                        })
                    }

                </div>
                <br />
            </div>
        </section>
    )
}
export default Favorites