import React from 'react';
import axios from 'axios';
import { Route, Routes } from 'react-router-dom';
import './App.css';
import Header from './components/header/Header';
import Footer from './components/footer/Footer';
import Cart from './components/cart/Cart';
import Favorites from './components/favorites/Favorites';
import Home from './components/Home';
import Burger from './components/header/Burger';

function App() {

  // state для хранения товаров
  const [products, setProducts] = React.useState([]);
  //state состояние корзины
  const [cartOpened, setCartOpened] = React.useState(false);
  // state для хранения товаров в корзине
  const [cartItems, setCartItems] = React.useState([]);
  // state для поиска
  const [search, setSearch] = React.useState('')
  // state для хранения избранных товаров
  const [favoritesItems, setFavoritesItems] = React.useState([])
  // state для хранения состояния загрузки
  const [loading, setLoading] = React.useState(true);
  // меню бургер
  const [burgerOpen, setBurgerOpen] = React.useState(false);

  /*useEffect - хук эффекта - дает возможность выполнять побочные эффекты в функциональном компоненте. В данном случае побочным будет fetch 
  
  Fetch - интерфейс ява скрипта по протоколу http (хотя )
then - это компонент promise - компонент создающий и связывающий в яваскрипте
а именно
fetch('ссылка на продукт') - это часть создающая и отдающая
myJson - атрибут безымянной функции
then - с помощью этого получаем и записываем в атрибут (потребляющий)
Последняя запись },[]) - фиксит саму перезапись цикла! запрос отправится один раз (заглушка)

но лучше использовать axios
  */
  React.useEffect(() => {
    // useEffect не может быть асинхронным но может модержать асинхрон
    async function axiosData() {
      
      //нужно чтобы загружались сначала карт и фаворитс а в конце продуктс
      //получаем занесенные данные корзины с сервера даже после перезагрузки страницы
      const cartData=await axios.get('https://my-json-server.typicode.com/maunisse/json-server/cart')
      const favoritesData=await axios.get('https://my-json-server.typicode.com/maunisse/json-server/favorites')
      const productsData=await axios.get('https://my-json-server.typicode.com/maunisse/json-server/products')

      setLoading(false)

      setCartItems(cartData.data)
      setFavoritesItems(favoritesData.data)
      setProducts(productsData.data)
    }
    axiosData()
  }, [])

// // получаем данные по ссылке
// // then = тогда
  // axios.get('http://localhost:3000/products').then((res) => {
    // // отправляем их на страницу
  //   setProducts(res.data)
  // })
  // //получаем занесенные данные корзины с сервера даже после перезагрузки страницы
  // axios.get('http://localhost:3000/cart').then((res) => {
  //   setCartItems(res.data)
  // })
  // axios.get('http://localhost:3000/favorites').then((res) => {
  //   setFavoritesItems(res.data)
  // })


  //удаление элемента корзины
  const onRemoveCartItem = (id) => {
    //обратные кавычки чтобы в конце прописать js код ${id}
    axios.delete(`https://my-json-server.typicode.com/maunisse/json-server/cart/${id}`)
    //передаем обновленные данные: отфильтровываем и оставляем все кроме того который удалялся
    //(prev) -возьми все предыдущие данные и отфильтруй их (внутри просто название аргумента который можно придумывать как угодно)
    //привели все к Number чтобы не возникало конфликта с макапи (чтобы удалялось равномерно а не все разом)
    setCartItems((prev) => prev.filter(item => Number(item.id) !== Number(id)))
  }
  //проверяем есть ли товар который мы выбрали-удалили для корректности отображения кнопок
  //проверяется при каждом нажатии на кнопку
  const itemAdded=(id)=>{
    return cartItems.some((objCart)=>objCart.id===id)
  }
  const itemFavorited=(id)=>{
    return favoritesItems.some((objFavorite)=>objFavorite.id===id)
  }

  return (
    //value передаем то что хотим передать в контекст
    <AppContext.Provider value={{
      products, 
      cartItems, 
      favoritesItems,
      setCartItems,
      setFavoritesItems,
      itemAdded,
      itemFavorited
      }}>

    <div className="app">

      {cartOpened ? <Cart
        onRemoveCartItem={onRemoveCartItem}
        cartItems={cartItems}
        closeCart={() => setCartOpened(false)}
        totalPrice={
          //reduce складывает данные из массива (числа) , totalElements - в качестве итогового значения и каждый раз при добавлении price будет прибавляться к этому итогу, 0 -начало отсчета
          cartItems.reduce((totalElements,objPrice)=>totalElements+objPrice.price,0)
        }
        /> : null}

      {burgerOpen ? <Burger
        closeBurger={() => setBurgerOpen(false)}
        /> : null}

      <Header 
        openCart={() => setCartOpened(true)} 
        cartItems={cartItems}
        openBurger={() => setBurgerOpen(true)}
      />

      <div className='container_section'>

        {/*react-route - старая библиотека которая работает с линками-ссылками \\в index.js сначала установили!*/}
        <Routes>
          {/*вывели компонент чтобы посередине было пусто от контента (однако в целом так злоупотреблять не нужно: лучше когда есть отдельная страница!)*/}
          <Route path='/maunisse-test/favorites' element={
            <Favorites
            //убираем теперь лишнее - то что задали через контекст
              // favoritesItems={favoritesItems}
              // setFavoritesItems={setFavoritesItems}
              // cartItems={cartItems}
              // setCartItems={setCartItems}
            />
          }
          />
          <Route path='/maunisse-test' element={
            <Home
              items={products}
              cartItems={cartItems}
              setCartItems={setCartItems}
              setSearch={setSearch}
              search={search}
              favoritesItems={favoritesItems}
              setFavoritesItems={setFavoritesItems}
              loading={loading}
              
            />
          }
          />
        </Routes>

      </div>

      <Footer />

    </div>
    </AppContext.Provider>
  );
}

// контекст - параметр, который можно использовать в любом компоненте напрямую
//он нам поможет с корректным обновлением состояния кнопок

export const AppContext = React.createContext({})
export default App;
