import style from './onas.module.css'

const Onas = () => {
    return (
        <section>
            <div className='block_for_jakor' id="o_nas_jakor"></div>
            
            <h2>О НАС</h2>
            <div className={style.o_nas_flex}>
                <p>
                    Делаем крутые букеты аж с 18** мохнатого года! Фамильное дело и качество!
                    <br /><br />
                    Приходите к нам в салон на экскурсию и увидите кучу древних фоток с дедами и прадедами, бабулями и пробабулями, которые делали свое цветочное дело.
                    <br /><br />
                    Когда нас спрашивают, куда мы движемся и будем ли меняться? Мы отвечаем: конечно же будем! И меняемся. День ото дня мы становимся лучше. Материалы для сборки цветочных композиций становятся все доступнее и интереснее. Появляется все больший простор для творчества.
                    <br /><br />
                    Приходите к нам и заказывайте наши произведения из мира флоры !
                </p>
                <p>
                    <img src={process.env.PUBLIC_URL +'/img/o_nas/Depositphotos_251878035_l-2015-min.jpg'} alt = "" />
                </p>
            </div>
            <br /><br />
        </section>
    )
}

export default Onas