import style from './banner.module.css'

const Banner = () => {
  return (
    <section>
      <div className={style.section_banner}>
      <div className={style.banner}>
        <div className={style.block_banner}>
          <p className={style.text_banner_one}>Лучшие цветы для лучших !</p>
        </div>
        <div className={style.block_banner}>
          <p className={style.text_banner_two}>Сделай счастливыми дорогих и близких!</p>
        </div>
        <div className={style.block_banner}>
          <a href='#product_jakor'><button className={style.banner_btn}>ВЫБРАТЬ</button></a>
        </div>
      </div>
      </div>
    </section>
  )
}

export default Banner