import style from './footer.module.css'

const Footer = () => {
  return (
    <footer id="footer">
      <div className={style.footer_section}>
        <div className={style.logo}><img src={process.env.PUBLIC_URL + '/img/header/logo.png'} alt = ""/></div>
        <p>Адрес: Томск, пр.Ленина, д.1 (есть ПАРКОВКА)
          <br /><br />
          Работаем с 10.00 до 19.00 каждый день (доставка КРУГЛОСУТОЧНАЯ)
          <br /><br />
          Тел: <b>+7-900-900-90-90</b>
          <br /><br />
          Курьера можно выбрать. Есть небольшая база проверенных надежных водителей мужского, женского и обоего пола. Они всегда у нас на связи.
        </p>
      </div>
    </footer>
  )
}

export default Footer