import React from "react"
import Slider from "react-slick"
import style from './sliderMy.module.css'


const SliderMy = () => {
    const settings = {
        dots: true,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 3500,
        cssEase: "linear"
    }
    return(
        
        <div className={style.slider_overflow}>
            <h3>Конкурс "Цветочки и ребята" !</h3>
            <br />
        <Slider {...settings}>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/1547142912_8.jpg'} alt = ""/></div>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/1649667896_11-vsegda-pomnim-com-p-zhivotnie-s-tsvetami-foto-12.jpg'} alt = ""/></div>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/Cute-cat-pink-flowers-hello-funny-animal_2560x1920.jpg'} alt = ""/></div>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/cvety-foto-belka-pozitiv.jpg'} alt = ""/></div>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/8c3cb9beef9f8ea203bf31c108522a54.jpg'} alt = ""/></div>
          <div className={style.slider_img}><img src={process.env.PUBLIC_URL +'/img/slider/1920x1200_1235604_[www.ArtFile.ru].jpg'} alt = ""/></div>
        </Slider>
        </div>
    )
}
export default SliderMy